import React from "react";

function Accordion(props) {
  const [isOpen, setOpen] = React.useState(false);

  const toggleBody = () => setOpen(prev => !prev);

  return (
    <div className="accordion">
      <div className="accordion-top container">
        <h2>{props.title}</h2>
        <button onClick={toggleBody}>{isOpen ? '-' : '+'}</button>
      </div>
      {isOpen ? (<div className="accordion-body container smallText">{props.body}</div>) : null}
    </div>
  );
}

export default Accordion;